}

.slider-nav__item, .field-bloks, .services__item, .categories__title, .product__item, .product__btn, .form__text, .swiper-buttons, .header__btn, .basket__btn-link, .basket__btn, .basket__item-inner, .basket__item, .sin-desc, .con-page__desc, .fill-btn, .filter__inner, .product-catalog, .calog-page-wrapper, .calog-page__inner, .ab-page__desc, .menu__liks, .menu, .header, .order-inner, .reviews-block__item, .new__inner-title, .about__inner-gallary-inner, .main__text, .title-block, .page__inner {
