}

.field {
  display: flex;
  align-items: center;
}
.field input {
  width: 60px;
  border: 1px solid #999;
  outline: none;
  background-color: #fff;
  font-size: 14px;
  font-family: Urbanist, sans-serif;
  margin-left: 10px;
  border-radius: 100px;
  padding: 4px;
  text-align: center;
  border-radius: 5px;
  -moz-appearance: textfield;
}
.field span {
  color: #fafafa;
  font-family: Urbanist, sans-serif;
  font-size: 16px;
  line-height: 20px;
}
.field-bloks {
  width: 100%;
}
.field-bloks .separator {
  color: #fff;
}

.slider {
  height: 5px;
  border-radius: 5px;
  position: relative;
  background: #919191;
}
.slider .progress {
  border-radius: 5px;
  background-color: #fafafa;
  height: 5px;
  left: 25%;
  right: 25%;
  position: absolute;
}

.range-input {
  position: relative;
}
.range-input input {
  position: absolute;
  pointer-events: none;
  top: 0px;
  height: 5px;
  width: 100%;
  background: none;
  -webkit-appearance: none;
}

input[type=range]::-webkit-slider-thumb {
  height: 17px;
  width: 17px;
  pointer-events: auto;
  border-radius: 50%;
  -webkit-appearance: none;
  background: #fbfbfb;
}

input[type=range]::-moz-range-thumb {
  height: 17px;
  width: 17px;
  border-radius: none;
  pointer-events: auto;
  border-radius: 50%;
  -moz-appearance: none;
  background: #fbfbfb;
}

.count {
  padding: 4px 10px;
  background-color: #fff;
  border: 1px solid #d1d1d1;
  border-radius: 4px 0 0 4px;
  height: 40px;
  font-size: 16px;
  font-family: "Urbanist", sans-serif;
}

select {
  border: 2px solid #000000;
  border-radius: 4px;
  width: 200px;
  height: 38px;
  padding: 6px 9px;
  font-family: Urbanist, sans-serif;
}

.pages {
  margin-right: 20px;
}

.pagination__option {
  margin: 50px 0px;
}

.pagination {
  list-style-type: none;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  align-items: center;
  justify-content: center;
}

.pagination__item {
  margin-right: 8px;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 33px;
  flex: 0 0 33px;
  width: 33px;
  height: 33px;
}

@media (min-width: 576px) {
  .pagination__item {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 40px;
    flex: 0 0 40px;
    width: 40px;
    height: 40px;
  }
}
.pagination__item_active .pagination__link {
  border-color: #453866;
  background-color: #453866;
  color: #fff;
}

.pagination__link {
  background-color: transparent;
  font-size: 16px;
  font-family: Vetrino;
  line-height: 25px;
  height: 100%;
  width: 100%;
  color: #000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  text-decoration: none;
  border-radius: 50%;
  -webkit-transition: border-color 0.25s ease, color 0.25s ease, background-color 0.25s ease;
  -o-transition: border-color 0.25s ease, color 0.25s ease, background-color 0.25s ease;
  transition: border-color 0.25s ease, color 0.25s ease, background-color 0.25s ease;
  border-color: #fff;
  border: 1px solid #453866;
  color: #000;
}

.pagination__link:hover {
  text-decoration: none;
  border-color: #453866;
  color: #fff;
  background-color: #453866;
}

.anim.right-header {
  transform: translate(-20%);
  transition: all 2s;
  opacity: 0;
}

.anim.right-header.active {
  transform: translateX(0);
  opacity: 1;
}

.card {
  display: flex;
  height: 100%;
  padding: 40px;
  border-radius: 20px;
  justify-content: space-between;
}
.card-title {
  width: 80%;
}
.card-slider {
  width: 592px;
  height: 435px;
  display: flex;
}
.card-info {
  width: 100%;
}

.slider-block {
  overflow: hidden;
  border-radius: 4px;
  width: calc(100% - 100px - 60px);
}
.slider-block__inner {
  width: 450px;
  height: 435px;
}
.slider-block__inner img {
  width: 100%;
  height: 100%;
}
.slider-nav {
  width: 100px;
  height: 100px;
  margin-right: 20px;
}
.slider-nav__item {
  overflow: hidden;
  border-radius: 4px;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.59);
}
.slider-nav__item:not(:last-child) {
  margin-bottom: 10px;
}

@media (max-width: 1200px) {
  .card {
    gap: 50px;
    align-items: center;
    flex-direction: column;
  }
  .card-info {
    align-items: center;
    max-width: 600px;
    justify-content: center;
  }
}
@media (max-width: 768px) {
  .card {
    gap: 50px;
    padding: 0;
    align-items: center;
  }
  .card-slider {
    justify-content: space-between;
    flex-direction: row-reverse;
  }
}
@media (max-width: 375px) {
  .card {
    gap: 50px;
